.ListRow {
    width: 98%;
    margin: 5px auto;
    color: white;
    border: 1px solid black;
    box-shadow: 0 2px 3px black;
}

.ListRow strong {
    color: grey;
}

.TopRow {
    display: -webkit-flex;
    display: flex; 
    text-align: left;
}

.TopRowLeft {
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    padding-left: 10px;
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
}

.TopRowRight {
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
}

.BottomRow {
    display: -webkit-flex;
    display: flex; 
    font-size: 12px;
}

.BottomRowLeft {
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    padding-left: 10px;
    text-align: left;
}

.BottomRowRight {
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    text-align: left;
    padding-right: 10px;
    display: -webkit-flex;
    display: flex;
}

.BottomRowRightA {
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    text-align: left;
    padding-left: 10px;
}

.BottomRowRightB {
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    text-align: right;
}